
.App {
  margin: 0 auto;
  width: 75%;

  font-size: large;
}

h1 {
  text-align: center;
}

.adventureSection {
  padding: 20px;
  border-radius: 10px;
  margin: 20px;
}

.adventureSection:hover {
  background-color: aliceblue;
}

.choicetaskoptioncontainer {
  display: flex;
  flex-wrap: wrap;
}

.choicetaskoption {
  flex-basis: calc(50% - 100px);
  padding: 20px;
  margin: 20px;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  border-radius: 10px;
  text-align: center;
}

.choicetaskoption button {
  padding: 10px;
  margin: 10px;
  width: 100%;
}

.showtask button{
  width: 350px;
  padding: 20px;
  font-size: large;
}

.integertask {
  padding: 20px;
  border-radius: 10px;
}

.integertask button {
  padding: 20px;
  font-size: large;
}

.integertask input {
  padding: 20px;
  font-size: large;
}

.ytContainer iframe {
  width: 600px;
  height: 339px;
}

.controlCenter {
  display: flex;
  justify-content: space-between;

  background-color: rgb(136, 174, 199);
  padding: 20px;
  margin-top: 20px;
  border-radius: 10px;
}

.controlCenter button {
  padding: 20px;
  font-size: larger;
}

.adventureSectionEditable {
  margin-bottom: 180px;
}

.listEditor {
  display: flex;
  flex-wrap: wrap;
}

.listEditorElem {
  flex-basis: calc(50% - 100px);
  padding: 20px;
  margin: 20px;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  text-align: center;
}

