
/* A single card in the deck */

.embeddedcard {
    background-image: url("./cardtest.svg");
    flex-shrink: 0;
    flex-grow: 0;
    margin: 10px;
    padding: 10px;
    text-align: center;
    width: 200px;
    height: 250px;
    border: 1px solid black;
    border-radius: 20px;
    box-shadow: 5px 5px 10px grey;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
  }
  
  .flavorLocal {
    background-image: url("./cardtest.svg");
  }
  
  .flavorGlobal {
    background-image: url("./cardtest2.svg");
  }
  
  .embeddedCardSubtype {
    margin-top: 20px;
    flex-basis: 50px;
    flex-grow: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .embeddedCardSubtype > img {
    object-fit: contain;
    width: 32px;
    height: 32px;
    margin-right: 10px;
  }
  
  .embeddedCardSubtype > p {
    margin-left: 10px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-size: x-small;
  }
  
  .embeddedCardImage {
    align-self: center;
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
  
  .embeddedCardName {
    flex-basis: 50px;
    font-size: large;
    margin-bottom: 20px;
    padding-top:  20px;
  }
  
  .embeddedCardBlurb {
    flex-basis: 150px;
  }
  
  /* When a card is hovered */
  
  .embeddedcard:hover {
    box-shadow: 0 0 100px 100px rgba(255, 255, 255, 0.3) inset; /* This is a hacky way to lighten the cards when they are hovered. Can't use background color because the hue varies with the flavor. */
  }
  
  /* Card Racks */
  
  .cardRack {
    display: flex;
    background-color: lightgrey;
    padding: 20px;
    border-radius: 20px;
    border: 1px solid black;
    overflow-y: scroll;
  }
  